
import { mapState } from "vuex";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'

import UserDialog from "../../components/UserDialog";
import dayjs from "dayjs";

export default {
directives: {
TransferDom
},
components: {
TransferDom,
XHeader,
XCircle,
UserDialog
},
beforeRouteEnter(to, from, next) {
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
} catch (err) {}
next();
},
beforeRouteLeave(to, from, next) {
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
this.$router.back();
});
} catch (err) {}
next();
},
mounted() {
this.UserID = this.$store.state.CurrentUserID;
this.setBriefDate();
this.setPlanDate();
this.getPlanInfo();
this.getBriefingInfo();
},
data() {
return {
showSelector1: false,
showSelector2: false,
planDateString: "",
planUserString: "我自己",
planParams: {
year: "",
month: "",
planType: 1,
selUserID: this.$store.state.CurrentUserID

},
planInfo: {
TotalMoney: 0,
PlanMoney: 0
},
briefDateString: "",
briefUserString: "我自己",
briefDate: {
beginDate: "",
endDate: "",
userId: 0
},
briefInfo: {
CustCount: 0,
FollowUpCount: 0,
OrderCount: 0,
OrderPrice: 0,
ReturnCount: 0,
ReturnPrice: 0
}
};
},
computed: {
...mapState(["isMyClient", "isAdmin"]),
planPercent() {
if (!this.planInfo.PlanMoney) {
return 0;
}
return (
Math.round(
(this.planInfo.TotalMoney / this.planInfo.PlanMoney) * 10000
) / 100
);
}
},

methods: {
showMonthPlugin(num) {
let _this = this;
this.$vux.datetime.show({
cancelText: "取消",
confirmText: "确定",
format: "YYYY-MM",
value: num == 1 ? this.planDateString : this.briefDateString,
onConfirm(val) {
if (num == 1) {
_this.planDateString = val;
_this.setPlanDate(val);
_this.getPlanInfo();
} else if (num == 2) {
_this.briefDateString = val;
_this.setBriefDate(val);
_this.getBriefingInfo();
}
}
});
},

showPeopleSelect(num) {
if (num == 1) {
this.showSelector1 = true;
} else {
this.showSelector2 = true;
}
},
hidePeopleSelect(num) {
if (num == 1) {
this.showSelector1 = false;
} else {
this.showSelector2 = false;
}
},
chooseUserForPlan(event) {
if (event.type == 1) {
this.planUserString = event.data.RealName;
this.planParams.selUserID = event.data.ID;
this.getPlanInfo();
}
},
chooseUserForBriefing(event) {
if (event.type == 1) {
this.briefUserString = event.data.RealName;
this.briefDate.userId = event.data.ID;
this.getBriefingInfo();
}
},
setPlanDate(date) {
let _date = "";
if (date) {
_date = dayjs(date);
} else {
_date = dayjs();
this.planDateString = _date.format("YYYY-MM");
}
this.planParams.year = _date.year();
this.planParams.month = _date.month() + 1;
},
setBriefDate(date) {
let _date = "";
if (date) {
_date = dayjs(date);
} else {
_date = dayjs();
this.briefDateString = _date.format("YYYY-MM");
}
this.briefDate.beginDate = _date
.startOf("month")
.format("YYYY-MM-DD HH:mm:ss");
this.briefDate.endDate = _date
.endOf("month")
.format("YYYY-MM-DD HH:mm:ss");
},
getPlanInfo() {
let url = "/GoalPlan/GetGoalPlanStatis";
this.getAxios(url, this.planParams).then(res => {
if (res.State == 1) {
this.planInfo = res.Data;
}
});
},
getBriefingInfo() {
let url = "/Statistics/GetDataCollection";
this.getAxios(url, this.briefDate).then(res => {
if (res.State == 1) {
this.briefInfo = res.Data;
}
});
},
goto(path) {
let params = {
...this.briefDate
};
this.$router.push({
path: path,
query: params
});
}
}
};



