
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XCircle from 'vux/src/components/x-circle/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from "@/components/scrollAll";
import Scroll1 from '@/components/scroll1'

import { mapState } from "vuex";
import NoData from "@/components/noData";
import storage from "good-storage";

export default {
directives: {
TransferDom
},
components:{
XButton,
TransferDom,
XCircle,
XDialog,
Tab,
TabItem,
Group,
Cell,
Scroll,
NoData,
XInput,
Icon,
Scroll1
},
mounted(){
this.UserID = this.$store.state.CurrentUserID;
this.getRecentSelect()
this.getUserData(1)
},
props:{
showDlg:{
type: Boolean,
default: false
},
showAll:{
type: Boolean,
default: true
},
showMe:{
type: Boolean,
default: true
}
},
watch:{
showDlg:function(newValue, oldValue){
this.showSelector = newValue;
if(newValue){
this.getRecentSelect()
}
}
},
data(){
return {
UserID:0,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
showSelector: this.showDlg,    //人员选择器控器
showUser: 0,                   //tab页控制
historyUserList: [],           //选择历史列表
userList:[],                    //人员列表
userPage:1,                     //人员分页
keyWord:'',                     //人员搜索关键字
groupList:[],                    //部门列表
}
},
computed:{
...mapState(["isMyClient", "isAdmin"])
},

methods:{

closeDlg(){
this.showSelector = false;
this.$emit("closeDlg", this.showSelector)
},

changePerson(index) {
this.showUser = index;
switch (index){
case 1: this.getUserData(1);return;
case 2: this.getGroup();return;
case 0: this.getRecentSelect(); return;
default: ;
}
},

getGroup() {
let orgID = this.$store.state.orgID || 0;
if (this.isAdmin) {
orgID = 0;
}
this.Axios.get(
`${api50}/api/v1/Org/GetOrgUserList?authType=2&orgId=${orgID}`
)
.then(res => {
res.data.Data.forEach(item => {

this.groupList.push(item)
});
this.groupList = res.data.Data.filter(item=>item.type==1)
})
.catch(err => {
console.log(err);
});
},

getNextOrg(){
console.log("next")
},

getRecentSelect(){
if (storage.get("historySelected")) {
let _list = storage.get("historySelected")[this.UserID]||[];
this.historyUserList = _list.reverse();
this.$refs.scroll1.forceUpdate();
this.$refs.scroll1.refresh();


}
},
setRecentSelect(item){
let _store = storage.get("historySelected");
if (_store) {
let user_store = _store[this.UserID];
if(user_store){
let sign = user_store.find(ele=>{return ele.data.ID == item.data.ID})
if(!sign){
user_store.push(item)
}
}else{
user_store = [item]
}
_store[this.UserID] = user_store
}else{
_store = {}
_store[this.UserID] = [item];
}
storage.set("historySelected", _store)
},


doSearch(){
this.getUserData(1)
this.$refs.scroll.scroll.finishPullUp();
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
},
getUserData(status) {
if (status === 1) {
this.userList = [];
this.userPage = 1;
} else {
this.userPage++;
}
this.getUserList();
},
getUserList() {
axios
.get(
`/User/GetPagedUserList?keyWords=${
this.keyWord
}&pageIndex=${this.userPage}&pageSize=10&v=0.8117229161569102`
)
.then(res => {
if (res.data.State == 1) {
if (this.userPage == 1) {
this.userList = res.data.Data.List;
this.$refs.scroll.scroll.finishPullDown();
} else {
this.userList.push(...res.data.Data.List);
this.$refs.scroll.scroll.finishPullUp();
}
}
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
})
.catch(e => {
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
});
},


selectorUser(type, item){
if(type == 1){
let _item = {type:type, data:item}
this.setRecentSelect(_item)
this.closeDlg()
this.$emit("chooseUser", _item)
}else if(type == 2){
this.setRecentSelect({type:type, data:item})
}else if(type == 3){
this.closeDlg()
this.$emit("chooseUser", {type:1,data:{ID:this.$store.state.CurrentUserID, RealName:"我自己"}})
}else if(type == 4){
this.closeDlg()
this.$emit("chooseUser", {type:1,data:{ID:0, RealName:"所有成员"}})
}
},

selectFromRecent(item){
this.closeDlg()
this.$emit("chooseUser", item)
}
},
}



